import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import validationClosingBarcod from "../Validations/validationClosingBarcod";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class ClosePengambilanBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleSubmit(data) {
    console.log(data);
  }
  render() {
    const { handleSubmit, pristine, reset, submitting } = this.props;
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <div className="card-body">
                <form onSubmit={handleSubmit}>
                  <div className="row">
                    <div className="col-md-4 col-lg-4">
                      <Field
                        name="noCloseJual"
                        label="No Close Jual"
                        type="text"
                        component={renderField}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <Field
                        name="jenisTrx"
                        label="Jenis Transaksi"
                        type="text"
                        component={renderField}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <Field
                        name="tanggal"
                        label="Tanggal"
                        type="date"
                        component={renderField}
                        className="form-control"
                      />
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

ClosePengambilanBarcode = reduxForm({
  form: "ClosePengambilanBarcode",
  validate: validationClosingBarcod,
  enableReinitialize: true,
})(ClosePengambilanBarcode);
export default ClosePengambilanBarcode;
